<template>
  <ui-loading :activeModel="!dataLoaded" :fullPage="true"></ui-loading>

  <button :type="type"
          :class="`btn ${btnColor}${btnBlock}`"
          :title="title"
          v-bind="$attrs"
          @click="listFile">
    <slot></slot>
  </button>

  <ui-file-list :value="fileList"
                @clickFile="onClickFile"
                v-if="0 < fileList.length">
  </ui-file-list>
</template>

<script>
import UiButton from './UiButton.vue';
import { messageUtil } from '@/utils';

export default {
  name: 'ui-file-button',
  extends: UiButton,
  props: {
    /**          key */
    listKey: String,
  },
  data() {
    return {
      /**   */
      fileList: [],
      /**     */
      dataLoaded: false,
    }
  },
  created() {
    this.dataLoaded = true;
  },
  methods: {
    /** cloudinary    */
    async listFile() {
      if (0 < this.fileList.length) {
        this.fileList = [];
        return;
      }

      this.dataLoaded = false;

      await (() => {
        return this.$http.get('/file')
        .then(res => {
          if (0 === res.data.length) {
            messageUtil.toastWarning('  .');
            return;
          }

          this.fileList = [...res.data];

          this.$emit('listFile', this.fileList, this.listKey);
        });
      })();
      
      this.dataLoading();
    },
    /** Cloudinary    */
    onClickFile(file) {
      this.$emit('clickFile', file, this.listKey);
    },
    /**   */
    dataLoading() {
      if (0 < this.fileList.length) {
        this.dataLoaded = true;
      }
    },
  },
}
</script>

